@import url(https://fonts.googleapis.com/css2?family=Lexend+Zetta&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,#root,.App{height:100%}body{font-family:'Lexend Zetta', sans-serif;background-image:linear-gradient(270deg, rgba(0,30,41,0.65), rgba(197,0,50,0.56)),url("/background.jpg");background-repeat:no-repeat;height:100%}@media (max-width: 480px){body{background-repeat:repeat}}

